.container {
    position: relative;
    width: 1170px;
    margin: 0 auto;
}
a {
    text-decoration: none;
}
@media (max-width: $tablet-width) {
    .container {
        position: relative;
        width: 320px;
        margin: 0 auto;
        z-index: 1;
    }
  }
  
  @media(min-width: $tablet-width) and (max-width: $desktop-width) {
    .container {
        position: relative;
        width: 748px;
        margin: 0 auto;
        z-index: 1;
        padding-left: 10px;
        padding-right: 10px;
    }
  }